.pricing {
    text-align: center;
    padding: 2rem;
  
    h2 {
      font-size: 35px;
      margin-bottom: 0.7rem;
      @media (max-width: 600px) {
       font-size: 20px;
      }
    }
  
    p {
      margin-bottom: 3rem;
      font-size: 14px;
      color: rgb(163, 163, 163);
      @media (max-width: 600px) {
        font-size: 10px;
       }
    }
  
    .pricing-toggle {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 2rem;
      gap: 1rem;
  
     
      .save {
        color: blue;
      }
  
      .switch {
        position: relative;
        display: inline-block;
        width: 50px;
        height: 25px;
  
        
        input {
          opacity: 0;
          width: 0;
          height: 0;
        }
  
        .slider {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          background-color: #ccc;
          transition: 0.4s;
          border-radius: 34px;
  
          &::before {
            position: absolute;
            content: '';
            height: 18px;
            width: 18px;
            left: 4px;
            bottom: 3px;
            background-color: white;
            transition: 0.4s;
            border-radius: 50%;
          }
        }
  
        input:checked + .slider {
          background-color: #4caf50;
        }
  
        input:checked + .slider::before {
          transform: translateX(25px);
        }
      }
    }
  
    .pricing-cards {
      display: flex;
      justify-content: center;
      gap: 1.5rem;
      @media (max-width: 600px) {
          flex-direction: column;
      }
  
      .card {
        background: #f9f9f9;
        border: 1px solid #ddd;
        padding: 2rem;
        width: 300px;
        border-radius: 12px;
        text-align: left;
        transition: box-shadow 0.3s ease;
  
        &:hover {
          box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
        }
  
        h3 {
          font-size: 1.5rem;
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
        p{
            font-size: 14px;
            color: rgb(135, 135, 135);
            margin-top: 10px;
        }
        h4 {
          font-size: 2rem;
          margin: 1.5rem 0;
          span {
            font-size: 1rem;
          }
        }
  
        button {
          background-color: #584dcf;
          color: white;
          width: 100%;
          border: none;
          padding: 0.75rem 1.5rem;
          border-radius: 8px;
          cursor: pointer;
          margin-bottom: 1.5rem;
          &:hover {
            background-color: #7f77d5;
          }
        }
  
        ul {
          list-style: none;
          padding: 0;
  
          li {
            padding: 0.5rem 0;
            font-size: 12px;
            display: flex;
            align-items: center;
            &:before {
              content: '';
              
              margin-right: 0.5rem;
          
            }
          }
        }
      }
  
      .most-popular {
        position: relative;
        border: 2px solid #584dcf;
  
        .popular-tag {
          background-color: #584dcf;
          color: white;
          padding: 0.2rem 0.5rem;
          border-radius: 5px;
          font-size: 0.75rem;
        }
      }
    }
  }
  


  .terms-conditions {
    margin-top: 30px;
    margin-left: 30px;

    

    .terms-list {
      margin-top: 20px;
      text-align: left;
      list-style-type: none;
      padding-left: 0;

      li {
        margin-bottom: 15px;
        font-size: 13px;
        color: #444;

        strong {
          font-weight: bold;
          color: #000;
        }
      }
    }}



    .smt{
      @media (max-width: 600px) {
        font-size: 12px;
       }
    }