.login-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 90vh;
    padding: 0 0rem;
    width: 100%;
  
    @media (max-width: 600px) {
      flex-direction: column;
      
    }
    .login-form {
      width: 40%;
      padding: 2rem 4rem;
      @media (max-width: 600px) {
       width: 100%;
       padding: 20px 10px;
      }
      
      h2 {
        font-size: 2rem;
        margin-bottom: 1rem;
        @media (max-width: 600px) {
          font-size: 20px;
        }
      }
  
      p {
        margin-bottom: 2rem;
        @media (max-width: 600px) {
          font-size: 12px;
        }
        a {
          color: #007bff;
          text-decoration: none;
        }
      }
  
      .form-group {
        margin-bottom: 1.5rem;
  
        label {
          display: block;
          font-weight: bold;
          margin-bottom: 0.5rem;
          font-size: 14px;
          font-weight: 500;
        }
  
        input {
          width: 100%;
          padding: 0.75rem;
          border: 1px solid #a4a4a4 !important;
          border-radius: 4px;
        }
      }
  
      .forgot-password {
        display: block;
        text-align: right;
        margin-top: 0.5rem;
        color: #007bff;
        text-decoration: none;
        font-size: 14px;
      }
  
      .login-button {
        width: 100%;
        padding: 1rem;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 4px;
        font-size: 1rem;
        cursor: pointer;
        @media (max-width: 600px) {
          padding: 10px;
          font-size: 12px;
        }
  
        &:hover {
          background-color: #0056b3;
        }
      }
  
      .alternative-login {
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
  
        button {
          padding: 1rem;
          font-size: 1rem;
          border: none;
          border-radius: 4px;
          cursor: pointer;
          display: flex;
          justify-content: center;
          align-items: center;
        }
  
        .google-login {
          background-color: #db4437;
          color: white;
        }
  
        .facebook-login {
          background-color: #3b5998;
          color: white;
        }
      }
    }
  
    .login-slider {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #f9f9f9;
      padding: 2rem;
      height: 90vh;
      @media (max-width: 600px) {
       width: 100%;
      }
      
  
      .slider-content {
        text-align: center;
  
        img {
          width: 500px;
          height: auto;
          margin-bottom: 1rem;
          border-radius: 8px;
        }
  
        h3 {
          font-size: 1.5rem;
          margin-bottom: 0.5rem;
        }
  
        p {
          margin-bottom: 2rem;
          color: #666;
          font-size: 0.8rem;
        }
  
        .slider-indicators {
          display: flex;
          justify-content: center;
          gap: 0.5rem;
  
          .indicator {
            width: 12px;
            height: 12px;
            background-color: #ddd;
            border-radius: 50%;
            cursor: pointer;
            transition: background-color 0.3s ease;
  
            &.active {
              background-color: #007bff;
            }
          }
        }
      }
    }
  }
  