.policydiv{
    width: 100%;
    height: 200px;
    background-color: rgb(219, 219, 255);
    display: flex;
    justify-content: center;
    align-items: center;

    h1{
        font-size: 50px;
        font-weight: 700;
        color: rgb(10, 10, 93);
        @media screen and (max-width: 600px) {
            font-size: 30px;
        }
    }
}



.privacy-policy {
    padding: 20px 100px;
    font-family: Arial, sans-serif;
    line-height: 1.6;
    color: #333;
    @media screen and (max-width: 600px) {
        padding: 20px 20px;
    }

    h1 {
        font-size: 16px;
        color: #0056b3;
        margin-bottom: 1.5rem;
        @media screen and (max-width: 600px) {
           font-size: 14px;
        }
    }

    h2 {
        font-size: 20px;
        color: #003366;
        margin-top: 1.5rem;
        margin-bottom: 1rem;
        @media screen and (max-width: 600px) {
            font-size: 16px;
        }
    }

    p, address {
        font-size: 16px;
        margin-bottom: 1rem;
        margin-left: 20px;
        @media screen and (max-width: 600px) {
            font-size: 12px;
        }

        strong {
            font-weight: 600;
            color: #0056b3;
        }
    }

    address {
        font-style: normal;
    }
}












.payment-page {
    display: flex;
    width: 100%;
    min-height: 400px;
    
    padding: 20px;
    @media screen and (max-width: 600px) {
        flex-direction: column;
    }

    .bank-details, .qr-code-section {
        width: 80%;
        padding: 20px;
        @media screen and (max-width: 600px) {
            width: 100%;
            padding: 10px;
        }
    }

    .bank-details {
        background-color: #ffffff;
        
        padding-left: 60px;
        @media screen and (max-width: 600px) {
            padding-left: 10px;
        }

        .bank-logo {
            width: 150px;
            margin-bottom: 20px;
            @media screen and (max-width: 600px) {
                width: 100px;
            }
        }

        h2 {
            font-size:20px;
            color: black;
            margin-bottom: 25px;
            @media screen and (max-width: 600px) {
                font-size: 16px;
            }
        }

        .bank-info {
            font-size: 14px;
            color: #333;

            @media screen and (max-width: 600px) {
               font-size: 12px;
            }

            p {
                margin-bottom: 10px;

                strong {
                    font-weight: bold;
                    color: #0056b3;
                }
            }
        }
    }

    .qr-code-section {
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #f5f8ff;
       
        border-radius: 8px;

        .qr-code-container {
            text-align: center;

            .qr-code {
                width: 400px;
                height: auto;
                margin-bottom: 20px;
                border-radius: 8px;
                box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
            }

            .qr-info {
                font-size: 14px;
                color: #0056b3;
            }
        }
    }
}






.terms-and-conditions {
    width: 100%;

    background-color: #ffffff;
  
    border-radius: 8px;
    padding: 20px 60px;
    margin-top: 20px;

    @media screen and (max-width: 600px) {
        padding: 20px 20px;
    }

    h3 {
        font-size: 22px;
        color: black;
        margin-bottom: 15px;
    }

    p {
        font-size: 16px;
        color: rgb(106, 106, 106);
        margin-bottom: 10px;
    }

    ul {
        padding-left: 20px;
        color: rgb(106, 106, 106);
        font-size: 14px;

        li {
            margin-bottom: 10px;
            list-style-type: disc;
        }
    }
}

