.testimonial-container {
    text-align: center; // Center the header
    
    // background-color: rgb(245, 245, 245);
    background-color: white;
    padding-top: 50px;
    padding-bottom: 50px;
  
    .header {
      font-size: 30px; // Header font size
      font-weight: bold; // Bold header
      margin-bottom: 30px;
    }
  
    .testimonial-background {
      display: flex; // Use flexbox to create background
      justify-content: center; // Center the boxes
      align-items: flex-start; // Align boxes to the top
     
      padding: 20px; // Padding for background
      border-radius: 10px; // Optional rounded corners for background
  
      .testimonial-boxes {
        display: flex; // Use flexbox for layout
        justify-content: center; // Center the boxes
        gap: 30px; // Gap between boxes
        @media (max-width: 600px) {
          width: 100%;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
  
        .testimonial-box {
          width: 300px; // Set a fixed width for all boxes
          padding: 20px; // Padding for content
          border:1px solid rgb(193, 193, 193);
          border-radius: 10px; // Rounded corners
          text-align: center; // Center content inside the box
          background: #fff; // White background for the box
  
          .person-image {
            width: 80px; // Set image width
            height: 80px; // Set image height
            border-radius: 50%; // Circle image
            margin: 0 auto; // Center the image
            display: block; // Display as block
            margin-bottom: 40px;
          }
  
          .review {
            margin: 15px 0; // Margin for review text
            font-size: 16px; // Font size for review
          }
  
          .stars {
            margin: 10px 0; // Margin for stars
            font-size: 20px; // Font size for stars
            color: rgb(226, 230, 12); // Yellow color for stars
          }
  
          .name {
            font-weight: bold; // Bold name
            margin: 5px 0; // Margin for name
          }
  
          .position {
            font-size: 14px; // Font size for position
            color: #777; // Optional color for position
          }
        }
      }
    }
  }













