  .landing-page {
    position: relative;
    width: 100%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
    background-image: url('https://salmon.blr1.digitaloceanspaces.com/Group%2015.png');
    background-size: 100% auto;; /* Fit the image within the content area */
    background-repeat: no-repeat; /* Prevents the image from tiling */
    background-position: center;
    @media screen and (max-width: 600px) {
      background-image: url('https://salmon.blr1.digitaloceanspaces.com/Group%201000005327.png')
  }

    
  
    /* Blob styling */
    .blob {
      position: absolute;
      opacity: 0.5;
      z-index: 1; /* Keep them behind content */
  
      &.blob-left {
        top: 10%;
        left: 2%;
        width: 600px;
        height: 500px;

        @media (max-width: 600px) {
          width: 150px;
          height: 150px;
        }
      }
  
      &.blob-right {
        top: 50%;
        right: 0%;
        width: 500px;
        height: 300px;
        @media (max-width: 600px) {
          width: 150px;
          height: 150px;
        }
      }
    }
  
    .top-section {
      text-align: center;
      max-width: 600px;
      margin-bottom: 40px;
  
      .badge {
        display: inline-block;
        padding: 5px 15px;
        background-color: #eef2ff;
        color: #5c6ac4;
        border-radius: 20px;
        font-size: 14px;
        margin-bottom: 15px;
        @media (max-width: 600px) {
          font-size: 12px;
        }
      }
  
      h1 {
        font-size: 40px;
        color:white;
        margin-bottom: 20px;
        font-weight: 600;
      }
  
      p {
        font-size: 16px;
        color:rgb(197, 197, 197);
        line-height: 1.6;
        margin-bottom: 30px;
        @media (max-width: 600px) {
          font-size: 13px;
        }
      }
  
      .buttons {
        display: flex;
        justify-content: center;
        gap: 15px;
  
        .btn-demo {
          background-color: #5c6ac4;
          color: white;
          padding: 10px 20px;
          border-radius: 30px;
          border: none;
          cursor: pointer;
          transition: background-color 0.3s ease;
          @media (max-width: 600px) {
            font-size: 12px;
          }
          &:hover {
            background-color: #4751a5;
          }
        }
  
        .btn-free {
          color: #5c6ac4;
          font-size: 16px;
          text-decoration: none;
          transition: color 0.3s ease;
          
  
          &:hover {
            color: #4751a5;
          }
        }
      }
    }
  
    .image-section {
      position: relative;
      width: 100%;
      max-width: 900px;
      display: flex;
      justify-content: center;
  
      .phone-image {
        max-width: 20%;
        height: auto;
      }
  
      .avatar {
        position: absolute;
        border: 4px solid white;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        overflow: hidden;
  
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
  
        &.avatar-left {
          top: 20%;
          left: 0;
          transform: translate(-50%, -50%);
        }
  
        &.avatar-right {
          top: 20%;
          right: 0;
          transform: translate(50%, -50%);
        }
  
        &.avatar-bottom {
          bottom: -20px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }









  .trading-feature {
    display: flex;
