.trade-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  
    .trade-row {
      display: flex;
      gap: 10px;
      flex-wrap: wrap;
      justify-content: space-evenly;
  
      .trade-card {
        background-color: #fff;
        border-radius: 10px;
        border: 1px solid rgb(207, 207, 207);
        width: 340px;
        padding: 10px;
        display: flex;
        flex-direction: column;
       
        
  
        .trade-title {
          font-size: 15px;
          font-weight: 500;
          margin-bottom: 10px;
        }
  
        .trade-info {
          display: flex;
          gap: 10px;
         
          margin-bottom: 15px;
  
          .time {
            background-color: #eceff1;
            padding: 5px 5px;
            border-radius: 5px;
            font-size: 12px;
          }
  
          .index {
            background-color: #eceff1;
            padding: 5px 10px;
            border-radius: 5px;
            font-size: 12px;
          }
  
          .match {
            background-color: #eceff1;
            padding: 5px 10px;
            border-radius: 5px;
            font-size: 12px;
          }
        }
  
        .trade-date {
          font-size: 12px;
          color: #666;
          margin-bottom: 10px;
        }
  
        .trade-description {
          font-size: 0.8rem;
          color: #999;
          margin-bottom: 20px;
        }
  
        .trade-actions {
          display: flex;
          gap: 2%;
  
          .btn {
            padding: 8px 16px;
            width: 48%;
            border: none;
            border-radius: 5px;
            cursor: pointer;
  
            &.take-live {
              background-color: #0c3152;
              color: white;
              font-size: 12px;
            }
  
            &.show-results {
              background-color: #eceff1;
              color: #333;
              font-size: 12px;
            }
          }
        }
      }
    }
  }
  















  .broker-container {
    max-width: 1200px;
    margin: 0 auto;
  
  
    h2 {
      font-size: 15px;
      font-weight: 500;
      margin-bottom: 10px;
    }
  
    .description {
      font-size: 13px;
      color: #666;
      margin-bottom: 20px;
    }
  
    .broker-row {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      flex-wrap: wrap;
  
      .broker-card {
        display: flex;
        background-color: #fff;
        border-radius: 10px;
       
        width: calc(50% - 20px);
        padding: 15px 20px;
        align-items: center;
        transition: transform 0.2s ease-in-out;
  
        &:hover {
          transform: translateY(-5px);
        }
  
        .broker-image-container {
          background-color: #f1f3f5; /* Light background color for the image */
          padding: 20px;
          border-radius: 10px;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 20px;
          height: calc(100% - 5px);
            width: 40%;
          .broker-logo {
            max-width: 100px;
          }
        }
  
        .broker-info-container {
          display: flex;
          flex-direction: column;
          justify-content: center;
          width: 100%;
  
          h3 {
            font-size: 15px;
            margin-bottom: 10px;
            text-transform: capitalize;
          }
  
          .broker-info {
            font-size: 12px;
            color: #666;
            margin-bottom: 20px;
          }
  
          .broker-actions {
            display: flex;
            gap: 10px;
  
            .btn {
              padding: 6px 10px;
              border: none;
              border-radius: 5px;
              width: 48%;
              cursor: pointer;
              font-size: 14px;
  
              &.connect {
                background-color: #0d453d;
                color: #fff;
              }
  
              &.open-account {
                background-color: #f1f1f1;
                color: #0d453d;
                display: flex;
                align-items: center;
                justify-content: center;
  
                span {
                  margin-left: 5px;
                }
              }
            }
          }
        }
      }
    }
  }
  














.custom-container {
  background-color: #ffffff; /* White background */
  border: 0.5px solid #818197; /* Black border */
  border-radius: 5px; /* Rounded corners */
  padding: 10px; /* Increased padding for inner spacing */
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align text to the left */
  margin: 20px; /* Combined margin */
}

.container-wrapper {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 10px;
}

.tag-container {
  background-color: rgb(75, 79, 153);
  color: white;
  border-radius: 10px;


  display: flex;
  align-items: center;
  justify-content: center;
  padding:10px 30px;
  font-size: 12px;
}

/* Style for h3 */
.custom-container h3 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #333333;
}

/* Style for p tag */
.custom-container p {
  font-size: 14px;
  color: #555555;
  margin-top: 0; /* Align p with h3 spacing */
}
/* Minimize spacing between first h3 and p */
.first h3 {
  font-size: 16px;
  font-weight: bold;
  color: #333333;
  margin-bottom: 5px; /* Reduce margin to minimize spacing */
}

.first1 p {
  font-size: 12px;
  color: #555555;
  margin-top: 0; /* Remove top margin */
}
.second h3 {
  color: #2421e0;
}