.services {
    max-width: 1200px;
    margin: 0 auto;
    padding: 40px 20px;
    text-align: center;
    color: #333;

    h1 {
        font-size: 28px;
        font-weight: 600;
        color: #000;
    }

    .intro-text {
        font-size: 13px;
        color: #838383;
        max-width: 800px;
        margin: 0 auto 40px;
    }

    .services-grid {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
        gap: 40px;
        margin-top: 30px;

        @media screen and (max-width: 600px) {
            grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        }
    }

    .service-card {
        background-color: #ffffff;
        border-radius: 8px;
        padding: 30px 20px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        text-align: left;



        .service-logo {
            width: 140px;
          
            margin-bottom: 15px;
            @media screen and (max-width: 600px) {
                width: 100px;
            }
        }

        h2 {
            font-size: 1.2rem;
            font-weight: 500;
            color: #333;
            margin-bottom: 10px;
            @media screen and (max-width: 600px) {
                font-size: 16px;
            }
        }

        p {
            font-size: 0.9rem;
            color: #666;
            line-height: 1.5;
            @media screen and (max-width: 600px) {
                font-size: 12px;
            }
        }
    }
}
